import React from 'react'
import './Template.css'
import Signup from '../pages/Signup'
import Login from '../pages/Login'
import frameImage from '../assets/frame.png'
import SignupForm from './SignupForm'
import LoginForm from './LoginForm'

export const Template = ({title,desc1,desc2,setLoggedin,formtype,image}) => {
  return (
    <div>
       <div className="flex">
       <div className="f1">
            <h1>{title}</h1>
            <p>
                <span>{desc1}</span> <br />
                <span>{desc2}</span>
            </p>
            {
                formtype == "signup" ?
                (<SignupForm setLoggedin={setLoggedin} />) :
                (<LoginForm setLoggedin={setLoggedin} />)
            }
       </div>

       <div className="f2">
            <img className='big1 big' src={frameImage} alt="" />
            <img className='big2 big' src={image} alt="" />
       </div>
       </div>
    </div>
  )
}

export default Template