import React from 'react'
import './LoginForm.css'
import { useState } from 'react'
import { LuEyeOff } from "react-icons/lu";
import { LuEye} from "react-icons/lu";
import { BrowserRouter as Router, Route,Routes, Link,NavLink } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import {FcGoogle} from 'react-icons/fc'


export const Login = ({setLoggedin}) => {

    let [formdata,setFormdata] = useState({email:"",password:""})
    let [showPassword, setshowPassword] = useState("password")
    let nav = useNavigate()
    
    function ChangeHandler(event)
    {
        // let {name,value,type} = event.target

        // console.log("Name : "+name)
        // console.log("Value " +value)
        // console.log("Type : "+type)
        console.log(formdata)
        
        setFormdata((prevdata) =>
        {
            return{
                ...prevdata,
                [event.target.name] : event.target.value
            }
        })
    }

    function SubmitHandler(event)
    {
        event.preventDefault()
        setLoggedin(true)
        toast.success("Logged in")
        nav('/dashboard')
        
    }

  return (
    <div className='login'>
        
        <form onSubmit={SubmitHandler}>
            <label htmlFor=""><p className='loginp'>Email Address <sup>*</sup></p>
                <input type="text" placeholder='Enter email address' name='email' value={formdata.email} onChange={ChangeHandler}  required/>
            </label>

            <label htmlFor=""><p className='loginp'>Password <sup>*</sup></p>   
                <input type={showPassword ? ("password") : ("text")} placeholder='Enter Password' name='password' value={formdata.password} onChange={ChangeHandler}  required   /> 
                {/* <span onClick={() => setshowPassword((prev) => !prev)} >
                    { {showPassword} ? <LuEye /> : <LuEyeOff /> }
                </span> */}
                <NavLink to="#"><p className='loginp'>Forget Password</p></NavLink>
            </label>

            <button className='longbt'>Sign in</button>
            <div className=''>
                <div></div>
                <div className='or'>OR</div>
                <div></div>
            </div>
            <button className='longbt longbt2'><span className='g'><FcGoogle /></span>Sign in With Google</button>

        </form>

    </div>
  )
}

export default Login