import React, { useState } from 'react'
import './SignupForm.css'
import { LuEyeOff } from "react-icons/lu";
import { LuEye} from "react-icons/lu";
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import {FcGoogle} from 'react-icons/fc'

export const Signup = ({setLoggedin}) => {
    let [formdata,setFormdata] = useState({fname:"",lname:"",email:"",password:"",cpass:""})
    let [showPassword, setshowPassword] = useState("password")
    let [Account,setAccount] = useState("student")

    let nav = useNavigate()

    function ChangeHandler(event)
    {
        // let {name,value,type} = event.target

        // console.log("Name : "+name)
        // console.log("Value " +value)
        // console.log("Type : "+type)
        console.log(formdata)
        
        setFormdata((prevdata) =>
        {
            return{
                ...prevdata,
                [event.target.name] : event.target.value
            }
        })
    }

    function SubmitHandler(event)
    {

        event.preventDefault()
        if(formdata.password != formdata.cpass)
        {
            toast.error("Password Mismatch")
        }
        else
        { 
            setLoggedin(true)
            toast.success("Account Created")
            nav('/dashboard')
            console.log("All Data")
            console.log(formdata)
        }
    }

    function AccountHandler()
    {
        if(Account == "student")
        {
            setAccount("insructor")
            document.querySelector('.in').classList.add('run')
            document.querySelector('.st').classList.remove('run')
        }
        else
        {
            setAccount("student")
            document.querySelector('.in').classList.remove('run')
            document.querySelector('.st').classList.add('run')
        }
    }

  return (
    <div className='su'>
        <div className="">
            <button className='st run'  onClick={AccountHandler}>Student</button>
            <button className='in' onClick={AccountHandler}>Instructor</button>
        </div>

        <form onSubmit={SubmitHandler}>
            <div className="flex">
                <label className='short' htmlFor="">
                    <p>First Name <sup>*</sup></p>
                    <input className='short' type="text" placeholder='First Name' name='fname' value={formdata.fname} onChange={ChangeHandler} required/>
                </label>

                <label htmlFor="">
                    <p>Last Name <sup>*</sup></p>
                    <input className='short' type="text" placeholder='Last Name' name='lname' value={formdata.lname} onChange={ChangeHandler} required/>
                </label>
            </div>

            <label htmlFor=""><p className='em'>Email Address<sup>*</sup></p>
                <input className='email' type="email" placeholder='Email address' name='email' value={formdata.email} onChange={ChangeHandler} required/>
            </label>

            <div className="flex">
                <label htmlFor=""><p>Create Password<sup>*</sup></p>
                <input className='short' type="password" placeholder='Enter password' name='password' value={formdata.password} onChange={ChangeHandler} required/>

                {/* <span onClick={() => setshowPassword((prev) => !prev)} >
                { {showPassword} ? <LuEye /> : <LuEyeOff /> }
                </span> */}
                </label>

                <label htmlFor=""><p>Confirm Password<sup>*</sup></p>
                <input className='short' type="password" placeholder='Enter password' name='cpass' value={formdata.cpass} onChange={ChangeHandler} required/>

                {/* <span onClick={() => setshowPassword((prev) => !prev)} >
                { {showPassword} ? <LuEye /> : <LuEyeOff /> }
                </span>  */}
                </label>
            </div>

            <button className='longbt'>Create Account</button>
            <div className=''>
                <div></div>
                <div className='or'>OR</div>
                <div></div>
            </div>
            <button className='longbt longbt2'><span className='g'><FcGoogle /></span> Sign in With Google</button>

        </form>
    </div>
  )
}

export default Signup