import React from 'react'
import { BrowserRouter as Router, Route,Routes, Link,NavLink, useNavigate } from 'react-router-dom';
import logo from '../assets/Logo.svg'
import './Navbar.css'
import toast from 'react-hot-toast'

export const Navbar = ({Loggedin,setLoggedin}) => {
    let nav = useNavigate()

    function LoginClickHandler(event)
    {
        setLoggedin(true)
        // toast.success("Loggedin Succesfully")
    }

    function LogoutClickHandler()
    {
        setLoggedin(false)
        toast.success("Logged Out Succesfully")
    }
  return (
    <div className='nav'>
        
        <nav>
        <NavLink to="/"><img className='logo' src={logo} alt="" /></NavLink>
            <ul>
                <li><NavLink to="/">Home</NavLink></li>
                <li><NavLink to="/about">About</NavLink></li>
                <li><NavLink to="/contact">Contact</NavLink></li>
            </ul>
            <div className="">
                { !Loggedin  &&
                    <NavLink to="/login"><button onClick={LoginClickHandler}>Login</button></NavLink>
                }
                { !Loggedin &&
                    <NavLink to="/signup"><button >Signup</button></NavLink>
                }
                { Loggedin &&
                    <NavLink to="/"><button onClick={LogoutClickHandler}>Log Out</button></NavLink>
                }
                { Loggedin &&
                    <NavLink to="/dashboard"><button>Dashboard</button></NavLink>
                }
            </div>
        </nav>
    </div>
  )
}

export default Navbar